#Projects {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); 
  gap: 20px;
  padding: 2rem 0;
}

#shareYourThoughts {
  position: relative;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 6px 4px 6px rgba(0, 0, 0, 0.1);
}

.GA-projects-container.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 1rem;
}

.simon-project, .TTT-project {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  max-width: 300px;
  margin: 0 auto;
}

.simon, .TTT {
  width: 60%;
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}
